const PublicationsComponent = () => {
  const [publications, setPublications] = useState(publicationsData);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortConfig, setSortConfig] = useState({ key: null, direction: 'ascending' });

  const sortedPublications = useMemo(() => {
    let sortableItems = [...publications];
    if (sortConfig.key !== null) {
      sortableItems.sort((a, b) => {
        if (a[sortConfig.key] < b[sortConfig.key]) {
          return sortConfig.direction === 'ascending' ? -1 : 1;
        }
        if (a[sortConfig.key] > b[sortConfig.key]) {
          return sortConfig.direction === 'ascending' ? 1 : -1;
        }
        return 0;
      });
    }
    return sortableItems;
  }, [publications, sortConfig]);

  const filteredPublications = useMemo(() => {
    return sortedPublications.filter(pub => 
      pub.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      pub.authors.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [sortedPublications, searchTerm]);

  const handleSearchChange = (e) => {
    setSearchTerm(e.target.value);
  };

  const requestSort = (key) => {
    let direction = 'ascending';
    if (sortConfig.key === key && sortConfig.direction === 'ascending') {
      direction = 'descending';
    }
    setSortConfig({ key, direction });
  };

  const getSortIcon = (key) => {
    if (sortConfig.key === key) {
      return sortConfig.direction === 'ascending' ? <ArrowUp size={16} /> : <ArrowDown size={16} />;
    }
    return null;
  };

  const handleRowClick = (doi) => {
    window.open(doi, '_blank', 'noopener,noreferrer');
  };

  return (
    <Container className="my-5">
      <Row className="mb-3">
        <Col>
          <Form.Control 
            type="text" 
            value={searchTerm} 
            onChange={handleSearchChange} 
            placeholder="Chercher par titre ou auteur" 
          />
        </Col>
      </Row>

      <Table hover>
        <thead>
          <tr>
            <th style={{width: '60%', cursor: 'pointer'}} onClick={() => requestSort('title')}>
              Titre {getSortIcon('title')}
            </th>
            <th className="text-center" style={{cursor: 'pointer'}} onClick={() => requestSort('citations')}>
              Citations {getSortIcon('citations')}
            </th>
            <th className="text-center" style={{cursor: 'pointer'}} onClick={() => requestSort('year')}>
              Année {getSortIcon('year')}
            </th>
          </tr>
        </thead>
        <tbody>
          {filteredPublications.map((pub, index) => (
            <tr 
              key={index} 
              onClick={() => handleRowClick(pub.doi)}
              style={{cursor: 'pointer'}}
            >
              <td>
                <h5 style={{color: '#1a0dab', marginBottom: '0.5rem'}}>
                  {pub.title}
                </h5>
                <p style={{fontStyle: 'italic', color: '#545454', marginBottom: '0.25rem'}}>{pub.authors}</p>
                <p style={{color: '#D5A00F', marginBottom: '0'}}>{pub.venue}</p>
              </td>
              <td className="text-center align-middle">{pub.citations}</td>
              <td className="text-center align-middle">{pub.year}</td>
            </tr>
          ))}
        </tbody>
      </Table>
      {filteredPublications.length === 0 && (
        <p className="text-center mt-4">Aucune publication ne remplit les critères.</p>
      )}
    </Container>
  );
};

export default PublicationsComponent;